.wp-block-cjp-blocks-section {
	background-color: rgba(0, 0, 0, 0);
	padding: 1px 1rem;
	box-sizing: border-box;
	max-width: 100%;
	transition: background-color 0.3s ease-in;
	margin: 28px 0;

	.svg-container {
		display: flex;
		align-items: center;
		justify-content: center;
	}

	svg {
		fill: #fff;
		width: 100px;
	}
}

.wp-block-cjp-blocks-section.background-clear {
	background-image: linear-gradient(90deg, #ffffff 1rem, rgba(0, 0, 0, 0) 1rem);
}

.wp-block-cjp-blocks-section.background-peach-800 {
	background-image: linear-gradient(90deg, #8f5f5a 1rem, rgba(0, 0, 0, 0) 1rem);
}

.wp-block-cjp-blocks-section.background-blue-600 {
	background-image: linear-gradient(90deg, #60656c 1rem, rgba(0, 0, 0, 0) 1rem);
}

.wp-block-cjp-blocks-section.background-peach-100 {
	background-image: linear-gradient(90deg, #f1e1d9 1rem, rgba(0, 0, 0, 0) 1rem);
}

